import React from "react";
import c from './Dialog.module.css';
import { NavLink } from "react-router-dom";

const Dialog = (props) => {
    const path = `/messages/${props.id}`;

    return (
        <div className={c.item}>
            <NavLink to={path}>
                {props.name}
            </NavLink>
        </div>
    )
} 

export default Dialog;