import React, { useEffect, useMemo } from "react";
import c from './Profile.module.css'
import ProfileInfo from "./ProfileInfo/ProfileInfo";
import MyPosts from "./MyPosts/MyPostsContainer";
import ProfileInfoContainer from "./ProfileInfo/ProfileInfoContainer";
import { setProfileThunk, getStatusThunk } from "../../redux/profile-reducer";
import { useDispatch, useSelector } from "react-redux";
import { useParams } from "react-router-dom";

const Profile = React.memo((props) => { 

    const dispatch = useDispatch()

    const profile = useSelector( state => state.profilePage.profileInfo)
    const status = useSelector(state => state.profilePage.status)

    const match = useParams()

    useEffect( () => {
        let userId = match.userId;

        if (!userId) {
            userId = 30359
        }

        dispatch(setProfileThunk(userId))
        dispatch(getStatusThunk(userId))

    }, [])

    console.log('this render prof')

    return (
        <main className={c.main}>
            <ProfileInfo profile={profile} status={status} />
            <MyPosts />
        </main>
    )
})

