import React, { memo } from "react";
import c from './MyPosts.module.css'
import Post from "./Post/Post";
import { addPost, updateNewPostText } from "../../../redux/profile-reducer";
import { useDispatch, useSelector } from "react-redux";

const MyPosts = memo((props) => {
    
    const posts = useSelector(state => state.profilePage.posts)
    const newPostText = useSelector(state => state.profilePage.newPostText)

    const dispatch = useDispatch()
    
    const postsElements = posts.map(p => <Post name={p.name} mes={p.mes} likes={p.likesCount} />)

    const newPostElement = React.createRef();

    const updateNewPostTextFunc = () => {
        
        const text = newPostElement.current.value;
        dispatch(updateNewPostText(text));
    }

    const addPostFunc = () => {
        dispatch(addPost())
    }
    
    console.log('this render')
    return (
        <div className={c.myPosts}>
            <div>
                <h3>My Posts</h3>
                <textarea onChange={updateNewPostTextFunc} ref={newPostElement} value={newPostText} />
            </div>
            <div onClick={addPostFunc}>
                <button>new post</button>
            </div>
            <div>
                {postsElements}
            </div>
        </div>
    )
})

export default MyPosts;