import React from "react";
import c from './News.module.css';
import NewsItem from "./NewsItem/NewsItem";

const News = (props) => {
    
    const newsRef = React.createRef();

    const updateNewsText = () => {
        const text = newsRef.current.value;
        props.updateNewNewsText(text)
    }

    const reloadNews = () => {
        props.reloadNews()
    }

    const newsElements = props.newsItems.map( (news) => <NewsItem id={news.id} category={news.category} message={news.message} />)

    return (
        <div className={c.news}>
            <textarea onChange={updateNewsText} ref={newsRef} value={props.newsMessage} />
            <button onClick={reloadNews}>
                reload
            </button>
            {newsElements}
        </div>
    )
}

export default News;