import React from "react";
import c from './NewsItem.module.css';
import { NavLink } from "react-router-dom";

const NewsItem = (props) => {

    return (
        <div>
            <NavLink to={`/news/${props.id}`}>
                <div className={c.item}>
                    <p>{props.category}</p>
                    <h3>{props.message}</h3>
                </div>
            </NavLink>
        </div>
    )
}

export default NewsItem;