import React, { useEffect } from "react";
import c from './Messages.module.css';
import Dialog from "./Dialog/Dialog";
import Chat from "./Chat/Chat";
import { BrowserRouter, NavLink, Navigate, Route, Routes } from "react-router-dom";
import Message from "./Chat/Message/Message";
import { updateNewMessageText, sendMessage } from "../../redux/messages-reducer";
import { useDispatch, useSelector } from "react-redux";

const Messages = (props) => {

    const state = useSelector( state => {
        return {
            dialogs: state.messagesPage.dialogs,
            messages: state.messagesPage.messages,
            newMessageText: state.messagesPage.newMessageText,
        }
    })

    const dispatch = useDispatch()

    const messageData = React.createRef();
    
    const updateNewMessage = () => {
        const text = messageData.current.value;
        dispatch(updateNewMessageText(text))
    }
    
    const sendMessageFunc = () => {
        dispatch(sendMessage())
    }

    const dialogsElements = state.dialogs.map(dialog => <Dialog name={dialog.name} id={dialog.id} />)
    
    const messagesElements = state.messages.map(m => <Message msg={m.msg}/>)

    return (
        <div className={c.main}>
            <div className={c.dialogs}>
                {dialogsElements}
            </div>
            <div className={c.input}>
                <textarea onChange={updateNewMessage} ref={messageData} value={state.newMessageText} placeholder="Enter message" />
                <div onClick={sendMessageFunc}>
                    <button>send</button>
                </div>
            </div>
            <div className={c.chat}>
                {messagesElements}
            </div>
        </div>
    )
}

export default Messages;