import React from "react";
import { reloadNews, updateNewNewsText } from "../../redux/news-reducer";
import News from "./News";
import { connect } from "react-redux";

const mapStateToProps = (state) => {
    return {

        newsItems: state.newsPage.newsItems,
        newsMessage: state.newsPage.newsMessage,

    }
}

const NewsContainer = connect(mapStateToProps, {reloadNews, updateNewNewsText})(News)

export default NewsContainer;