import React from "react";
import c from './Header.module.css'
import { connect } from "react-redux";
import { loginUserThunk } from "../../redux/auth-reducer";
import Header from "./Header";

class authAPI extends React.Component {

    componentDidMount() {
        
        this.props.loginUserThunk()
    }

    render() {
        
        return <Header {...this.props} />
    }
}

const mapStateToProps = (state) => (

    {
        id: state.authInfo.id,
        email: state.authInfo.email,
        login: state.authInfo.login,
    }

)

const HeaderContainer = connect(mapStateToProps, {loginUserThunk})(authAPI)

export default HeaderContainer;