import React, { memo } from "react";
import c from './Post.module.css'

const Post = memo(({name, mes, likes}) => {
    
    return (
        <div className={c.item}>
            <div className={c.logo}>
                <img src="https://statusneo.com/wp-content/uploads/2023/02/MicrosoftTeams-image551ad57e01403f080a9df51975ac40b6efba82553c323a742b42b1c71c1e45f1.jpg"></img>
            </div>
            <div className={c.info}>
                <div className={c.user}>
                    {name}
                </div>
                <div className={c.text}>
                    {mes}
                </div>
                <div className={c.like}>
                    likes: {likes}
                </div>
            </div>
        </div>
    )
})

export default Post;