const UPDATE_NEW_NEWS_TEXT = 'UPDATE-NEW-NEWS-TEXT';
const RELOAD_NEWS = 'RELOAD-NEWS';

const initialState = {
    newsItems: [
        {id: 1, category: 'Design', message: 'Today is good for designers'},
        {id: 2, category: 'Games', message: 'New game is realased today'},
        {id: 3, category: 'Business', message: 'New currency'},
    ],
    newsMessage: '',
}

const newsReducer = (state = initialState, action) => {

    switch (action.type) {

        case UPDATE_NEW_NEWS_TEXT: {

            return {
                ...state,
                newsMessage: action.message,
            };
        }

        case RELOAD_NEWS: {
            const newsItem = {
                id: 4, category: 'Computers', message: state.newsMessage,
            };

            return {
                ...state,
                newsMessage: '',
                newsItems: [...state.newsItems, newsItem]
            };
        }

        default: return state;
    }
}

export const updateNewNewsText = (text) => (
    {type: UPDATE_NEW_NEWS_TEXT, message: text}
);

export const reloadNews = () => (
    {type: RELOAD_NEWS}
)

export default newsReducer;