import { profileAPI } from "../api/api";

const ADD_POST = 'ADD-POST';
const DELETE_POST = 'DELETE_POST';
const UPDATE_NEW_POST_TEXT = 'UPDATE-NEW-POST-TEXT'; 
const SET_PROFILE = 'SET_PROFILE';
const SET_STATUS = 'SET_STATUS';
const UPDATE_STATUS_TEXT = 'SET_STATUS_TEXT';

const initialState = {
    posts: [
        { id: 1, name: 'Dmitriy', mes: 'This is my first post', likesCount: 23 },
        { id: 2, name: 'Alex', mes: 'Hi! How are you?', likesCount: 12 },
        { id: 3, name: 'Marry', mes: "I'm very happy!", likesCount: 2 },
        { id: 4, name: 'Bob', mes: 'Hey guys', likesCount: 9 },
        { id: 5, name: 'John', mes: 'I really love my family', likesCount: 32 },
        ],
    newPostText: '',
    profileInfo: {},
    status: '',
}

const profileReducer = (state = initialState, action) => {

    switch (action.type) {
        
        case UPDATE_NEW_POST_TEXT: {

            return {
                ...state,
                newPostText: action.message,
            };
        }

        case ADD_POST: { 
            const newPost = {
                id: 1, name: 'Dmitriy', mes: state.newPostText, likesCount: 0,
            }


            return {
                ...state,
                posts: [...state.posts, newPost],
                newPostText: ''
            };
        }

        case DELETE_POST: {
            return {
                ...state,
                posts: state.posts.filter(p => p.id != action.id)
            }
        }

        case SET_PROFILE: {
            return {
                ...state,
                profileInfo: action.profileInfo,
            }
        }

        case SET_STATUS: {
            return {
                ...state,
                status: action.status
            }
        }

        case UPDATE_STATUS_TEXT: {
            return {
                ...state,
                status: action.text
            }
        }

        default: return state;
    }
}

export const updateNewPostText = (text) => (
    {type: UPDATE_NEW_POST_TEXT, message: text}
);

export const addPost = () => (
    {type: ADD_POST}
);

export const deletePost = (id) => (
    {type: DELETE_POST, id}
);

export const setProfile = (Info) => (
    {type: SET_PROFILE, profileInfo: Info,}
)

const setStatus = (status) => (
    {type: SET_STATUS, status}
)

export const setProfileThunk = (userId) => async (dispatch) => {
    const response = await profileAPI.getProfileInfo(userId)
    dispatch(setProfile(response))
}

export const getStatusThunk = (userId) => async (dispatch) => {
    const response = await profileAPI.getStatus(userId)
    dispatch(setStatus(response.data))
}

export const updateStatus = (status) => async (dispatch) => {
    const response = await profileAPI.updateStatus(status)
    if (response.resultCode === 0) {
        dispatch(setStatus(status))
    } else {
        dispatch(setStatus('error'))
    }
}

export default profileReducer;