import { usersAPI } from "../api/api";
import { setFollowed } from "../utilits/validators/setFollowedUtilita";

const FOLLOW = 'FOLLOW';
const UNFOLLOW = 'UNFOLLOW';
const SET_USERS = 'SET_USERS';
const CHANGE_PAGE = 'CHANGE_PAGE';
const SET_TOTAL_COUNT = 'SET_TOTAL_COUNT';
const CHANGE_FETCHING = 'CHANGE_FETCHING';
const CHANGE_FOLLOW_FETCHING = 'CHANGE_BUTTON_FETCHING';

const initialState = {

    users: [],
    isFetching: true,
    pageSize: 10,
    totalUsersCount: 0,
    currentPage: 1,
    followIsFetching: [],

}

const usersReducer = (state = initialState, action) => {
    
    switch (action.type) {
        
        case FOLLOW: {
            debugger;
            return {
                ...state,
                users: setFollowed(state.users, {followed: true}, action.userId)
                /*state.users.map( u => {
                    if (u.id === action.userId) {
                        
                        return {...u, followed: true}

                    }
                    return u;
                })*/
            }
        }

        case UNFOLLOW: {
            const stateCopy = {
                ...state,
                users: state.users.map( (el) => {
                    if (el.id === action.userId) {
                
                        return {
                            ...el,
                            followed: false,
                        }

                    }

                    return el;
                })
            }

            return stateCopy;
        }

        case SET_USERS: {
            return {
                ...state,
                users: [...action.users],
            }
        }

        case CHANGE_PAGE: {
            return {
                ...state,
                currentPage: action.page,
            }
        }

        case SET_TOTAL_COUNT: {
            return {
                ...state,
                totalUsersCount: action.totalCount
            }
        }
        
        case CHANGE_FETCHING: {
            return {
                ...state,
                isFetching: action.isFetching,
            }
        }
        case CHANGE_FOLLOW_FETCHING: {
            return {
                ...state,
                followIsFetching: 
                    action.isFetching ? [...state.followIsFetching, action.userId] :
                    [...state.followIsFetching.filter(id => id != action.userId)]
            }
        }

        default: return state;
    }
}

export const follow = (userId) => (
    {type: FOLLOW, userId: userId }
)

export const unfollow = (userId) => (
    {type: UNFOLLOW, userId: userId}
)

export const setUsers = (users) => (
    {type: SET_USERS, users: users}
)

export const changePage = (page) => (
    {type: CHANGE_PAGE, page: page}
)

export const setTotalCount = (totalCount) => (
    {type: SET_TOTAL_COUNT, totalCount: totalCount,}
)

export const setFetchingChange = (fetchingExists) => (
    {type: CHANGE_FETCHING, isFetching: fetchingExists}
)

export const setFollowFetchingChange = (isFetching, userId) => (
    {type: CHANGE_FOLLOW_FETCHING, isFetching, userId}
)

export const getUsersThunk = (currentPage, pageSize) => async (dispatch) => {
    dispatch(setFetchingChange(true))
    const response = await usersAPI.getPage(currentPage, pageSize)
    dispatch(setFetchingChange(false))
    dispatch(setUsers(response.items));
    dispatch(setTotalCount(response.totalCount))
}

export const onPageChangeThunk = (pageNum, pageSize) => async (dispatch) => {
        dispatch(setFetchingChange(true));
        dispatch(changePage(pageNum));
        const response = await usersAPI.pageChange(pageNum, pageSize)
        dispatch(setFetchingChange(false));
        dispatch(setUsers(response.items))
}

export const followUnfollowFlow = async (dispatch, userId, apiMethod, actionCreator) => {
    dispatch(setFollowFetchingChange(true, userId))
    const response = await apiMethod(userId)
    if (response.resultCode === 0) {
        dispatch(actionCreator(userId))
        setFollowFetchingChange(false, userId)
    }
}

export const followUserThunk = (userId) => (dispatch) => {
    const apiMethod = usersAPI.followUser;
    const actionCreator = follow;
    followUnfollowFlow(dispatch, userId, apiMethod, actionCreator)
}

export default usersReducer;