import React, { useEffect } from "react";
import c from './Header.module.css'
import { NavLink } from "react-router-dom";
import Preloader from "../../commonComponents/Preloader";
import { useDispatch, useSelector } from "react-redux";
import { loginUserThunk } from "../../redux/auth-reducer";

const Header = (props) => {

    const state = useSelector( state => {
        return {
            id: state.authInfo.id,
            email: state.authInfo.email,
            login: state.authInfo.login,
        }
    })

    const dispatch = useDispatch()

    useEffect( () => {
        
        dispatch(loginUserThunk())
    }, [])

    /*if (!props.id) {
        return <Preloader />
    }*/

    return (
    <header className={c.header}>
        <img src='https://1000logos.net/wp-content/uploads/2022/05/Fansly-Logo.png' className={c.img}></img>
        {!state.id ? 
        <div className={c.login}>
            <NavLink to={`/login`}>
                <div>login</div>
            </NavLink>
            <button>register</button>
        </div> :
        <span>{state.login}</span>
    }
    </header>
    )
}

export default Header;