import { loginAPI } from "../api/api";

const LOGIN = 'LOGIN';

const initialState = {

    id: null,
    email: null,
    login: null,
    isAuth: false,
}

const authReducer = (state = initialState, action) => {
    
    switch (action.type) {
        
        case LOGIN: {
            
            return {
                ...state,
                ...action.data,
                isAuth: true,
            }
        }
        default: return state;
    }
}

export const loginUser = (id, email, login) => (
    {type: LOGIN, data: {id, email, login}}
)

export const loginUserThunk = () => async (dispatch) => {
    const response = await loginAPI.getLogin()
    if (response.resultCode === 0) {
        const { email, id, login } = response.data;
        dispatch(loginUser(id, email, login))
    }
}

export default authReducer;