import axios from "axios"
import React from "react"

const instance = axios.create({
    withCredentials: true,
    baseURL: 'https://social-network.samuraijs.com/api/1.0/',
    'API-KEY': '2ba1d219-2563-4664-bd9a-4daa2e364950'
})

export const profileAPI = {
    getProfileInfo(userId) {

        return instance.get(`profile/${userId}`).then(
            response => {
                return response.data
            }
        )
    },
    getStatus(userId) {
        return instance.get(`profile/status/${userId}`)
    },
    updateStatus(status) {
        
        return instance.put('profile/status', {status: status}).then(
            response => {
                return response.data
            }
        )
    }
}

export const usersAPI = {
    getPage(currentPage, pageSize) {
        return instance.get(`users?page=${currentPage}&count=${pageSize}`).then(
            response => {
                return response.data
            }
        )
    },
    pageChange(pageNum, pageSize) {
        return instance.get(`users?page=${pageNum}&count=${pageSize}`).then(
            response => {
                return response.data
            }
        )
    },
    followUser(userId) {
        return instance.post(`profile/follow/${userId}`).then(
            response => {
                return response.data
            }
        )
    },
    unfollowUser(userId) {
        return instance.delete(`profile/follow/${userId}`).then(
            response => {
                return response.data
            }
        )
    }
}

export const loginAPI = {
    getLogin() {
        return instance.get(`auth/me`).then(
            response => {
                
                return response.data
            }
        )
    }
}